import React from "react";
import { ListDiv, Listitem } from "../../Styles/ListCSS";
import { Link } from "react-router-dom";

//      
import moment from "moment";
//    
import "moment/locale/ko";

function List(props) {
  const SetTime = (a, b) => {
    if (a !== b) {
      return moment(b).format("YYYY MMMM Do  hh:mm ") + "()";
    } else {
      return moment(a).format("YYYY MMMM Do  hh:mm ");
    }
  };
  return (
    <ListDiv>
      {props.PostList.map((post, idx) => {
        return (
          <Listitem key={idx}>
            <Link to={`/post/${post.postNum}`}>
              <h3>{post.title}</h3>
              <p>{` : ${post.author.displayName}`}</p>
              <p>{post.content}</p>
              <p className="time">{SetTime(post.createdAt, post.updatedAt)}</p>
            </Link>
          </Listitem>
        );
      })}
    </ListDiv>
  );
}

export default List;
