import axios from "axios";
import React, { useEffect } from "react";
import {
  DetailtDiv,
  Detailitem,
  DetailButtonDiv,
} from "../../Styles/PostDetailCSS";
import { Link, useParams, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";

import moment from "moment";
import "moment/locale/ko";

function Detail(props) {
  let params = useParams();
  let navigate = useNavigate();

  const user = useSelector((state) => state.user);

  useEffect(() => {
    console.log(props.PostInfo);
  }, [props.PostInfo]);

  const DeleteHandler = () => {
    if (window.confirm("  ?")) {
      let body = {
        postNum: params.postNum,
      };
      axios
        .post("/api/post/delete", body)
        .then((res) => {
          if (res.data.success) {
            alert(" .");
            navigate("/");
          }
        })
        .catch((err) => {
          alert("  .");
        });
    }
  };

  const SetTime = (a, b) => {
    if (a !== b) {
      return moment(b).format("YYYY MMMM Do  hh:mm ") + "()";
    } else {
      return moment(a).format("YYYY MMMM Do  hh:mm ");
    }
  };

  console.log(props);

  return (
    <DetailtDiv>
      <Detailitem>
        <h3>{props.PostInfo.title}</h3>
        <strong>{props.PostInfo.author.displayName}</strong>
        {props.PostInfo.image ? (
          <img
            src={`http://localhost:8080/${props.PostInfo.image}`}
            alt=""
            style={{ width: "100%", heigth: "auto" }}
          />
        ) : null}
        <p>{props.PostInfo.content}</p>
        <p className="time">
          {SetTime(props.PostInfo.createdAt, props.PostInfo.updatedAt)}
        </p>
      </Detailitem>
      {user.uid === props.PostInfo.author.uid && (
        <DetailButtonDiv>
          <Link to={`/edit/${props.PostInfo.postNum}`}>
            <button className="editButton"></button>
          </Link>
          <button className="deleteButton" onClick={() => DeleteHandler()}>
            
          </button>
        </DetailButtonDiv>
      )}
    </DetailtDiv>
  );
}

export default Detail;
