import React, { useState } from "react";
import firebase from "../../firebase";
import axios from "axios";
import { LoginDiv } from "../../Styles/UserCSS";
import { useNavigate } from "react-router-dom";

function Register() {
  const [Name, setName] = useState("");
  const [Email, setEmail] = useState("");
  const [Pw, setPw] = useState("");
  const [PwConfirm, setPwConfirm] = useState("");
  const [Flag, setFlag] = useState(false);
  const [NameCheck, setNameCheck] = useState(false);
  const [NameInfo, setNameInfo] = useState("");

  let navigate = useNavigate();

  // 
  const RegisterFunc = async (e) => {
    setFlag(true);
    e.preventDefault();
    //   
    if (!(Name && Email && Pw && PwConfirm)) {
      return alert("  ");
    }
    if (Pw != PwConfirm) {
      return alert("  .");
    }
    if (!NameCheck) {
      return alert("  ");
    }
    //     
    // async        
    let createUser = await firebase
      .auth()
      .createUserWithEmailAndPassword(Email, Pw);

    //  Name    .
    await createUser.user.updateProfile({
      displayName: Name,
    });

    console.log(createUser);
    let body = {
      email: createUser.user.multiFactor.user.email,
      displayName: createUser.user.multiFactor.user.displayName,
      uid: createUser.user.multiFactor.user.uid,
    };
    axios.post("/api/user/register", body).then((res) => {
      setFlag(false);
      if (res.data.success) {
        navigate("/login");
      } else {
        alert(" .");
      }
    });
  };

  const NameCheckFunc = (e) => {
    e.preventDefault();
    if (!Name) {
      return alert(" ");
    }

    let body = {
      dispalyName: Name,
    };

    axios.post("/api/user/namecheck", body).then((response) => {
      if (response.data.success) {
        if (response.data.check) {
          setNameCheck(true);
          setNameInfo(" .");
        } else {
          setNameInfo(" .");
        }
      }
    });
  };

  return (
    <LoginDiv>
      <form>
        <label></label>
        <input
          type="name"
          value={Name}
          disabled={NameCheck}
          onChange={(e) => setName(e.currentTarget.value)}
        />
        {NameInfo}
        <button onClick={(e) => NameCheckFunc(e)}> </button>
        <label></label>
        <input
          type="email"
          value={Email}
          onChange={(e) => setEmail(e.currentTarget.value)}
        />
        <label></label>
        <input
          type="password"
          value={Pw}
          minLength={8}
          onChange={(e) => setPw(e.currentTarget.value)}
        />
        <label> </label>
        <input
          type="password"
          value={PwConfirm}
          minLength={8}
          onChange={(e) => setPwConfirm(e.currentTarget.value)}
        />
        <button disabled={Flag} onClick={(e) => RegisterFunc(e)}>
          
        </button>
      </form>
    </LoginDiv>
  );
}

export default Register;
