import React, { useState, useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import { RepleContentDiv, RepleUploadDiv } from "../../Styles/RepleCSS";
import axios from "axios";

import moment from "moment";
import "moment/locale/ko";

function RepleContent(props) {
  const [ModalFlag, setModalFlag] = useState(false);
  const [EditFlag, setEditFlag] = useState(false);
  const [Reple, setReple] = useState(props.reple.reple);

  const user = useSelector((state) => state.user);
  const ref = useRef();
  useOnClickOutside(ref, () => setModalFlag(false));

  const SubmitHandler = (e) => {
    e.preventDefault();

    let body = {
      uid: user.uid,
      reple: Reple,
      postId: props.reple.postId,
      repleId: props.reple._id,
    };

    axios.post("/api/reple/edit", body).then((res) => {
      if (res.data.success) {
        alert("  .");
      } else {
        alert("  .");
      }
      return window.location.reload();
    });
  };

  const DeleteHandler = (e) => {
    e.preventDefault();
    let body = {
      postId: props.reple.postId,
      repleId: props.reple._id,
    };
    axios.post("/api/reple/delete", body).then((res) => {
      if (res.data.success) {
        alert(" .");
      } else {
        alert("  .");
      }
      window.location.reload();
    });
  };

  const SetTime = (a, b) => {
    if (a !== b) {
      return moment(b).format("YYYY MMMM Do  hh:mm ") + "()";
    } else {
      return moment(a).format("YYYY MMMM Do  hh:mm ");
    }
  };

  return (
    <RepleContentDiv>
      <div className="author">
        <p>{props.reple.author.displayName}</p>
        {props.reple.author.uid === user.uid && (
          <div className="modalControl">
            <span onClick={() => setModalFlag(true)}></span>
            {ModalFlag && (
              <div className="modalDiv" ref={ref}>
                <p
                  onClick={() => {
                    setEditFlag(true);
                    setModalFlag(false);
                  }}
                >
                  
                </p>
                <p className="delete" onClick={(e) => DeleteHandler(e)}>
                  
                </p>
              </div>
            )}
          </div>
        )}
      </div>
      {EditFlag ? (
        <RepleUploadDiv>
          <form>
            <input
              type="text"
              value={Reple}
              onChange={(e) => setReple(e.currentTarget.value)}
            />
            <button onClick={(e) => SubmitHandler(e)}></button>
          </form>
          <div className="cancel">
            <button
              onClick={(e) => {
                e.preventDefault();
                setEditFlag(false);
              }}
            >
              
            </button>
          </div>
        </RepleUploadDiv>
      ) : (
        <>
          <p>{props.reple.reple}</p>
          <p className="time">
            {SetTime(props.reple.createdAt, props.reple.updatedAt)}
          </p>
        </>
      )}
    </RepleContentDiv>
  );
}

// Hook
function useOnClickOutside(ref, handler) {
  useEffect(() => {
    const listener = (event) => {
      if (!ref.current || ref.current.contains(event.target)) {
        return;
      }
      handler(event);
    };
    document.addEventListener("mousedown", listener);
    document.addEventListener("touchstart", listener);
    return () => {
      document.removeEventListener("mousedown", listener);
      document.removeEventListener("touchstart", listener);
    };
  }, [ref, handler]);
}

export default RepleContent;
